import React, { useState, useEffect } from "react";
import validator from "validator";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import Header from "../Header";
import { BASE_URL } from "../Url";

function StepTwo({ nextStep, handleFormData, prevStep, values }) {
  const [selectedState, setSelectedState] = useState([]);
  const [states, setStates] = useState([]);
  const [lgas, setLgas] = useState([]);
  const [hospitalData, setHospitalData] = useState([]);

  //creating error state for validation
  const [error, setError] = useState(false);

  // after form submit validating the form data using validator
  const submitFormData = (e) => {
    e.preventDefault();

    // checking if input values are empty to show error else take to next step
    if (
      validator.isEmpty(values.dose) ||
      validator.isEmpty(values.vaccine) ||
      validator.isEmpty(values.hospital) ||
      validator.isEmpty(values.date) ||
      validator.isEmpty(values.sessionTime) ||
      validator.isEmpty(values.stateName) ||
      validator.isEmpty(values.lga)
    ) {
      setError(true);
    } else {
      nextStep();
    }
  };

  //Get States from endpoint in BASE_URL
  useEffect(() => {
    const getStates = async () => {
      const response = await fetch(`${BASE_URL}`);
      const states = await response.json();
      if (typeof states !== "undefined") {
        setStates(states);
      }
    };
    getStates();
  }, []);

  // const handleStateChange = () => {
  //   const stateName = document.getElementById("state").value;
  //   console.log(stateName);
  //   let currentState = states.filter(
  //     (currentState) =>
  //       currentState.name === document.getElementById("state").value
  //   );
  //   currentState = [...new Set(currentState.map((item) => item.alias))];
  //   // console.log(currentState);
  //   setSelectedState(currentState);
  // };

  const handleStateChange = () => {
    const stateName = document.getElementById("state").value;
    console.log(stateName);
    setSelectedState(stateName);
    if (
      document.getElementById("state").value === "Federal Capital Territory"
    ) {
      setSelectedState("abuja");
    }
  };

  useEffect(() => {
    const getLgas = async () => {
      const response = await fetch(`${BASE_URL}${selectedState}/lgas`);
      const lgas = await response.json();
      if (typeof lgas !== "undefined") {
        setLgas(lgas);
      }
    };
    getLgas();
  }, [selectedState]);

  const handleLgaChange = () => {
    const currentLga = document.getElementById("lga").value;
    console.log(typeof currentLga);
    getHospitalData(currentLga);
  };

  const getHospitalData = async (currentLga) => {
    try {
      let response = await FunctionsProvider.getHospitals(currentLga);
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        return;
      }
      console.log(response.data);
      setHospitalData(response.data.hospitals);
    } catch (error) {
      console.log("Error is", error);
    }
  };

  return (
    <>
      <Header />
      {/****** STEPPER ******/}
      <div className="max-w-lg mx-auto p-4 flex justify-between items-center overflow-x-hidden md:overflow-x-visible">
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase text-gray-400">
            {/* Display Description */}
            Step 1
          </div>
          <div className="rounded-full transition duration-500 ease-in-out bg-gray-800 border-2 border-gray-800 text-white font-bold h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} &#10003;
          </div>
        </div>
        <div className="flex-auto border-t-2 transition duration-500 ease-in-out border-gray-700">
          {/* Display Line */}
        </div>
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
            {/* Display Description */}
            Step 2
          </div>
          <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} 2
          </div>
        </div>
        <div className="flex-auto border-t-2 transition duration-500 ease-in-out">
          {/* Display Line */}
        </div>
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
            {/* Display Description */}
            Step 3
          </div>
          <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} 3
          </div>
        </div>
      </div>
      {/***** END OF STEPPER *****/}
      <div className="max-w-7xl py-14 mx-auto">
        <h2 className="text-black font-bold text-3xl flex justify-center">
          Vaccination Information
        </h2>
        <form
          className="w-full max-w-5xl mx-auto py-6"
          onSubmit={submitFormData}
        >
          <div className="flex flex-wrap lg:-mx-3 py-3">
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="gender"
              >
                Which Covid-19 vaccine dose are you booking for?
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                name="dose"
                onChange={handleFormData("dose")}
                defaultValue={values.dose}
              >
                <option value="">-- Select Dose --</option>
                <option value="First">First dose</option>
                <option value="Second">Second dose</option>
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="occupation"
              >
                What vaccine would you like to be vaccinated with?
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                name="vaccine"
                onChange={handleFormData("vaccine")}
                defaultValue={values.vaccine}
              >
                <option value="">-- Type of vaccine --</option>
                <option value="Pfizer">Pfizer</option>
                <option value="Moderna">Moderna</option>
                <option value="Johnson &amp; Johnson">
                  Johnson &amp; Johnson
                </option>
                <option value="Oxford">Oxford, AstraZeneca</option>
                <option value="Novavax">Novavax</option>
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="occupation"
              >
                Preferred State for Vaccination
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                onChange={handleFormData("stateName", handleStateChange)}
                id="state"
                defaultValue={values.stateName}
              >
                <option value="">-- Select State --</option>
                {states.map((thisstate, index) => {
                  return (
                    <option key={index} value={thisstate.name}>
                      {/* {console.log(thisstate.name)} */}
                      {thisstate.name}
                    </option>
                  );
                })}
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="occupation"
              >
                Preferred LGA for Vaccination
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                onChange={handleFormData("lga", handleLgaChange)}
                id="lga"
                defaultValue={values.lga}
              >
                {lgas.length > 0 ? (
                  lgas.map((thislga, index) => {
                    return (
                      <option key={index} value={thislga}>
                        {thislga}
                      </option>
                    );
                  })
                ) : (
                  <option value="">-- Select LGA --</option>
                )}
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="occupation"
              >
                Preferred Center for Vaccination
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                name="hospital"
                onChange={handleFormData("hospital")}
                defaultValue={hospitalData.name}
              >
                <option value="">-- Select Vaccine Center --</option>
                {hospitalData.length >= 0 ? (
                  hospitalData.map((thishospital, index) => {
                    return (
                      <option key={index} value={thishospital.name}>
                        {/* {console.log(thishospital)} */}
                        {thishospital.name}
                      </option>
                    );
                  })
                ) : (
                  <option value="Ikoyi Medical Hospital">
                    There's no hospital in this LGA
                  </option>
                )}
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="grid-first-name"
              >
                Select your preferred date for vaccination
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                name="date"
                type="date"
                placeholder="DD/MM/YYYY"
                onChange={handleFormData("date")}
                defaultValue={values.date}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="occupation"
              >
                Select your preferred time of appointment
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                name="sessionTime"
                onChange={handleFormData("sessionTime")}
                defaultValue={values.sessionTime}
              >
                <option value="">-- Select session time --</option>
                <option value="Morning">Morning Session</option>
                <option value="Afternoon">Afternoon Session</option>
              </select>
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
          </div>

          <div className="mx-auto mt-10 flex justify-center">
            <button
              className="py-2 px-8 bg-white text-gray-700 border-2 border-gray-700 text-base font-bold rounded-lg mx-3"
              onClick={prevStep}
            >
              Back
            </button>
            <button className="py-2 px-8 bg-gray-700 text-white text-base font-bold rounded-lg focus:border-4 transition-colors duration-300 transform hover:bg-gray-800 border-gray-700">
              Next
            </button>
          </div>
        </form>
      </div>
    </>
  );
}

export default StepTwo;
