import React, { useState } from "react";
import validator from "validator";
import Header from "../Header";

function StepOne({ nextStep, handleFormData, values }) {
  const [error, setError] = useState(false);
  // after form submit validating the form data using validator
  const submitFormData = (e) => {
    e.preventDefault();

    // checking if input values are empty show error else take to step 2
    if (
      validator.isEmpty(values.booking_for) ||
      validator.isEmpty(values.firstName) ||
      validator.isEmpty(values.lastName) ||
      validator.isEmpty(values.email) ||
      validator.isEmpty(values.phoneNumber) ||
      validator.isEmpty(values.address) ||
      validator.isEmpty(values.dob)
    ) {
      setError(true);
    } else {
      nextStep();
    }
  };
  return (
    <>
      <Header />
      {/****** STEPPER ******/}
      <div className="max-w-lg mx-auto p-4 flex justify-between items-center overflow-x-hidden md:overflow-x-visible">
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
            {/* Display Description */}
            Step 1
          </div>
          <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} 1
          </div>
        </div>
        <div className="flex-auto border-t-2 transition duration-500 ease-in-out">
          {/* Display Line */}
        </div>
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
            {/* Display Description */}
            Step 2
          </div>
          <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} 2
          </div>
        </div>
        <div className="flex-auto border-t-2 transition duration-500 ease-in-out">
          {/* Display Line */}
        </div>
        <div className="relative flex flex-col items-center text-gray-700">
          <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
            {/* Display Description */}
            Step 3
          </div>
          <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
            {/* Display Number */} 3
          </div>
        </div>
      </div>
      {/******* END OF STEPPER *********/}
      <div className="max-w-7xl py-14 mx-auto">
        <h2 className="text-black font-bold text-3xl flex justify-center">
          Personal Information
        </h2>
        <p className="text-black font-normal text-lg max-w-xl text-center mx-auto py-4">
          To get started, you'll need to fill out this form. It will just take a
          couple of minutes.
        </p>
        <form
          className="w-full max-w-5xl mx-auto py-6"
          onSubmit={submitFormData}
        >
          <p className="font-bold text-base px-3 lg:px-0">
            I am booking this appointment and/or registering for: *
          </p>
          <div className="block py-3 px-3 lg:px-0">
            <div>
              <input
                className="appearance-none rounded-full h-4 w-4 border border-gray-300 bg-white checked:bg-gray-700 checked:text-gray-700 focus:outline-none transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain mr-2 float-left cursor-pointer"
                style={{ border: error ? "2px solid red" : "" }}
                type="radio"
                name="flexRadioDefault"
                value="Myself"
                onChange={handleFormData("booking_for")}
              />
              <label className="pl-4">Myself</label>
            </div>
            <div>
              <input
                className="appearance-none rounded-full h-4 w-4 border border-gray-300 bg-white checked:bg-gray-600 checked:text-gray-600 focus:outline-none transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain mr-2 float-left cursor-pointer"
                style={{ border: error ? "2px solid red" : "" }}
                type="radio"
                name="flexRadioDefault"
                value="Someone else"
                onChange={handleFormData("booking_for")}
              />
              <label className="pl-4 pt-1">Someone else</label>
            </div>
            {error ? (
              <div className="text-red-500 text-xs">
                This is a required field
              </div>
            ) : (
              ""
            )}
          </div>
          <div className="flex flex-wrap lg:-mx-3 py-3">
            <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="lastName"
              >
                Last Name (Surname)
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                type="text"
                name="lastName"
                defaultValue={values.lastName}
                placeholder="Enter Surname"
                onChange={handleFormData("lastName")}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="firstName"
              >
                First Name (Given name)
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                type="text"
                name="firstName"
                defaultValue={values.firstName}
                placeholder="Enter first name"
                onChange={handleFormData("firstName")}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
          <div className="flex flex-wrap lg:-mx-3 py-3">
            <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="grid-first-name"
              >
                Email
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                type="email"
                name="email"
                defaultValue={values.email}
                placeholder="Name@email.com"
                onChange={handleFormData("email")}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  Please enter a valid email address
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="phone-number"
              >
                Phone Number
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                type="number"
                defaultValue={values.phoneNumber}
                placeholder="+234867444444"
                onChange={handleFormData("phoneNumber")}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  Enter a valid phone number
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
          <div className="w-full px-3 lg:px-0 py-3">
            <label
              className="block tracking-wide text-gray-700 text-base font-bold mb-2"
              htmlFor="address"
            >
              Address
            </label>
            <input
              className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
              style={{ border: error ? "2px solid red" : "" }}
              name="address"
              type="text"
              defaultValue={values.address}
              placeholder="Enter home address"
              onChange={handleFormData("address")}
            />
            {error ? (
              <div className="text-red-500 text-xs">
                This is a required field
              </div>
            ) : (
              ""
            )}
          </div>
          <div className="flex flex-wrap lg:-mx-3 py-3">
            <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                htmlFor="gender"
              >
                Select Gender
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                onChange={handleFormData("gender")}
                defaultValue={values.gender}
              >
                <option value="">-- Select Gender --</option>
                <option value="male">Male</option>
                <option value="female">Female</option>
                <option value="diverse">Diverse</option>
              </select>
            </div>
            <div className="w-full md:w-1/2 px-3">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="occupation"
              >
                Occupation
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                id="occupation"
                type="text"
                defaultValue={values.occupation}
                placeholder="Occupation"
                onChange={handleFormData("occupation")}
              />
            </div>
          </div>
          <div className="flex flex-wrap lg:-mx-3 py-3">
            <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="grid-first-name"
              >
                Date of birth
              </label>
              <input
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                style={{ border: error ? "2px solid red" : "" }}
                type="date"
                defaultValue={values.dob}
                placeholder="Select Date"
                onChange={handleFormData("dob")}
              />
              {error ? (
                <div className="text-red-500 text-xs">
                  This is a required field
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="w-full md:w-1/2 px-3">
              <label
                className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                for="phone-number"
              >
                Type of ID
              </label>
              <select
                className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                onChange={handleFormData("idType")}
                defaultValue={values.idType}
              >
                <option value="">-- Valid means of ID --</option>
                <option value="International Passport">
                  International Passport
                </option>
                <option value="Drivers License">Drivers License</option>
                <option value="Voters Card">Voters Card</option>
                <option value="National ID Card">National ID Card</option>
              </select>
            </div>
            <div className="mx-auto mt-10">
              <button
                className="py-2 px-8 bg-gray-700 text-white text-base font-bold rounded-lg focus:border-4 transition-colors duration-300 transform hover:bg-gray-800 border-gray-700"
                type="submit"
              >
                Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default StepOne;
