import { PencilAltIcon } from "@heroicons/react/outline";
import React, { useEffect, useState } from "react";
import AdminHeader from "./AdminHeader";
import Sidebar from "./Sidebar";
import { useNavigate } from "react-router-dom";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import { useSnackbar } from "react-simple-snackbar";
import { getToken } from "../../Utils/Session";

function AdminVaccineCenter() {
  const [page, setPage] = useState(1);
  const [hospitalInfoData, setHospitalInfoData] = useState([]);
  const [token] = useState(getToken());
  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "17px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "14px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "17px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "14px",
    },
  };
  const [successMsg] = useSnackbar(options);
  const [errorMsg] = useSnackbar(options2);

  const getHospitalInfoData = async () => {
    try {
      let response = await FunctionsProvider.getHospitalInfo();
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        errorMsg("error fetching data", 5000);
        return;
      }
      setHospitalInfoData(response.data.hospitals);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  //endpoint to get all hospitals/vaccine center
  useEffect(() => {
    getHospitalInfoData();
  },[]);

  const navigate = useNavigate();

  const addVacCenter = () => navigate("/admin/add-vaccine-center");

  const [editCenter, setEditCenter] = useState({});

  //get values of the vaccines
  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    // console.log(value);
    return {
      value,
      onChange: handleChange,
    };
  };

  const pfizer = useFormInput("");
  const moderna = useFormInput("");
  const johnson = useFormInput("");
  const novavax = useFormInput("");
  const oxford = useFormInput("");

  //endpoint to edit a vaccine center/hospital
  const editVaccineCenter = async () => {
    try {
      let body = {
        name: editCenter.name,
        facilityId: editCenter.facilityId,
        state: editCenter.state,
        lga: editCenter.lga,
        pzifer: pfizer.value,
        moderna: moderna.value,
        johnson: johnson.value,
        novavax: novavax.value,
        oxford: oxford.value,
      };
      let response = await FunctionsProvider.editHospital(token, body);
      console.log("response is", response);
      if (response.data.status === "error") {
        errorMsg("An error occurred. Please try again", 5000);
        return;
      }
      console.log(response.data);
      successMsg(response.data.message, 5000);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  return (
    <>
      <AdminHeader />
      <div className="flex mt-2">
        <Sidebar />
        <div className="" style={{ flex: 4 }}>
          <div className="container py-8 px-6">
            {page === 1 && (
              <>
                <div className="flex justify-between items-center">
                  <div className="flex flex-col space-y-2 mb-2">
                    <h3 className="font-semibold text-lg">Vaccine Center</h3>
                    <p className="text-sm font-normal text-gray-400">
                      Edit amount of vaccines in a center
                    </p>
                  </div>
                  <div>
                    <button
                      className="bg-gray-700 text-white px-5 py-3 rounded-lg border-2 border-gray-700 hover:bg-transparent hover:text-gray-700 font-semibold"
                      type="button"
                      onClick={() => addVacCenter()}
                    >
                      Add Center
                    </button>
                  </div>
                </div>

                <div className="container inline-block min-w-full shadow rounded-lg overflow-hidden mt-5">
                  <table className="min-w-full leading-normal">
                    <thead>
                      <tr>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Vaccine Center
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Facility ID
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          State
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Lga
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Pfizer
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Moderna
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Johnson
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Novavax
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                          Oxford
                        </th>
                        <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {hospitalInfoData.length > 0 ? (
                        hospitalInfoData.map((hpidata, index) => {
                          return (
                            <tr key={index}>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.name}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.facilityId}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.state}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.lga}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.pzifer}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.moderna}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.johnson}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.novavax}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-no-wrap">
                                  {hpidata.oxford}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <PencilAltIcon
                                  className="w-6 h-6 text-gray-500 cursor-pointer"
                                  onClick={() => {
                                    setPage(2);
                                    setEditCenter(hpidata);
                                  }}
                                />
                              </td>
                            </tr>
                          );
                        })
                      ) : (
                        <tr className="flex justify-center mx-auto py-3">
                          <td>
                            <p className="text-gray-900 text-base whitespace-no-wrap">
                              No Vaccine Center has been added.
                            </p>
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </>
            )}
            {page === 2 && (
              <>
                <div className="flex flex-col mb-5 space-y-2">
                  <p
                    className="font-semibold text-sm text-gray-700 cursor-pointer"
                    onClick={() => setPage(1)}
                  >
                    Back
                  </p>
                  <h3 className="font-semibold text-lg">Edit Vaccine Center</h3>
                </div>
                <div className="flex flex-col lg:-mx-3 py-3">
                  <div className="w-full md:w-1/2 px-3 mb-8 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      Vaccine Center
                    </label>
                    {editCenter.name}
                    {/* {console.log(editCenter)} */}
                  </div>
                  <div className="w-full px-3 mt-5 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="quantity"
                    >
                      Quantity
                    </label>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-3 lg:gap-5 w-full mx-auto">
                      <div>
                        <label
                          className="block tracking-wide text-gray-700 text-sm font-medium mt-2 mb-1"
                          htmlFor="pfizer"
                        >
                          Pfizer
                        </label>
                        <input
                          className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                          type="number"
                          name="pfizer"
                          placeholder={editCenter.pzifer}
                          {...pfizer}
                        />
                      </div>
                      <div>
                        <label
                          className="block tracking-wide text-gray-700 text-sm font-medium mt-2 mb-1"
                          htmlFor="moderna"
                        >
                          Moderna
                        </label>
                        <input
                          className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                          type="number"
                          name="moderna"
                          placeholder={editCenter.moderna}
                          {...moderna}
                        />
                      </div>
                      <div>
                        <label
                          className="block tracking-wide text-gray-700 text-sm font-medium mt-2 mb-1"
                          htmlFor="johnson"
                        >
                          Johnson &amp; Johnson
                        </label>
                        <input
                          className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                          type="number"
                          name="johnson"
                          placeholder={editCenter.johnson}
                          {...johnson}
                        />
                      </div>
                      <div>
                        <label
                          className="block tracking-wide text-gray-700 text-sm font-medium mt-2 mb-1"
                          htmlFor="novavax"
                        >
                          Novavax
                        </label>
                        <input
                          className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                          type="number"
                          name="novavax"
                          placeholder={editCenter.novavax}
                          {...novavax}
                        />
                      </div>
                      <div>
                        <label
                          className="block tracking-wide text-gray-700 text-sm font-medium mt-2 mb-1"
                          htmlFor="oxford"
                        >
                          Oxford, Astrazaneca
                        </label>
                        <input
                          className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                          type="number"
                          name="oxford"
                          placeholder={editCenter.oxford}
                          {...oxford}
                        />
                      </div>
                    </div>
                  </div>
                  <div>
                    <button
                      className="bg-gray-700 text-white px-5 py-3 rounded-lg border-2 border-gray-700 hover:bg-transparent hover:text-gray-700 font-semibold mx-3 my-4"
                      type="button"
                      onClick={() => {
                        editVaccineCenter();
                      }}
                    >
                      Edit Center
                    </button>
                  </div>
                </div>
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default AdminVaccineCenter;
