import { CheckCircleIcon } from "@heroicons/react/outline";
import React from "react";
import { useNavigate } from "react-router-dom";

function SuccessModal({ closeModal }) {
  const navigate = useNavigate();

  const vaccineCenter = () => navigate("/admin/vaccine-center");
  return (
    <div className="w-screen h-screen fixed flex justify-center items-center bg-black bg-opacity-70 z-20 transition-opacity ease-in-out duration-500">
      <div
        className="w-full h-full inset-0 z-0 absolute"
        onClick={() => {
          closeModal(false);
        }}
      ></div>
      <div className="max-w-xs md:max-w-xl z-50 bg-white rounded-2xl p-8">
        <div className="max-w-lg mx-auto">
          <div className="flex flex-col justify-center items-center mx-auto text-center space-y-3">
            <CheckCircleIcon className="w-14 h-14 text-green-600 flex justify-center" />
            <h4 className="font-semibold text-gray-700 text-xl">
              Vaccine Center added Successfully!
            </h4>
          </div>
          <div className="my-8 flex justify-center space-y-3 md:space-y-0">
            <button
              className="mx-3 py-2.5 px-5 bg-transparent border border-gray-700 text-gray-700 rounded-lg hover:bg-gray-700 hover:text-white"
              onClick={() => {
                closeModal(false);
                vaccineCenter();
              }}
            >
              Back to Vaccine Center
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SuccessModal;
