import axios from "axios"; // some http client lib

const BASE_URL = process.env.REACT_APP_API_BASE_URL
  ? process.env.REACT_APP_API_BASE_URL
  : "http://localhost:5000/api/v1/cbs/";

function adminLogin(email, password) {
  return axios.post(`${BASE_URL}admin/login`, { email, password });
}

function bookings({
  firstName,
  lastName,
  phoneNumber,
  email,
  address,
  gender,
  occupation,
  dob,
  idType,
  dose,
  booking_for,
  vaccine,
  stateName,
  lga,
  hospital,
  date,
  sessionTime,
}) {
  return axios.post(`${BASE_URL}booking`, {
    firstName,
    lastName,
    phoneNumber,
    email,
    address,
    gender,
    occupation,
    dob,
    idType,
    dose,
    booking_for,
    vaccine,
    stateName,
    lga,
    hospital,
    date,
    sessionTime,
  });
}

function getBookings(token) {
  const headers = {
    "Content-Type": "application/json",
    "x-auth": token,
  };
  return axios.get(`${BASE_URL}bookings`, { headers });
}

function getBookingInfo(vacId) {
  return axios.get(`${BASE_URL}booking?vacId=${vacId}`);
}

function changeBookingInfo(vacId, dose, vaccine, hospital, date, sessionTime) {
  console.log("obj is", {
    vacId,
    dose,
    vaccine,
    hospital,
    date,
    sessionTime,
  })
  return axios.put(`${BASE_URL}booking`, {
    vacId,
    dose,
    vaccine,
    hospital,
    date,
    sessionTime,
  });
}

function deleteBookingInfo(vacId) {
  return axios.delete(`${BASE_URL}booking?vacId=${vacId}`);
}

function getHospitals(lga) {
  return axios.get(`${BASE_URL}hospitals?lga=${lga}`);
}

function getHospitalInfo() {
  return axios.get(`${BASE_URL}hospitals`);
}

function createHospitals(token, body) {
  const headers = {
    "Content-Type": "application/json",
    "x-auth": token,
  };
  return axios.post(`${BASE_URL}hospital`, body, {
    headers,
  });
}

function editHospital(token, body) {
  const headers = {
    "Content-Type": "application/json",
    "x-auth": token,
  };
  return axios.put(`${BASE_URL}hospital`, body, {
    headers,
  });
}

function getOverview(token) {
  const headers = {
    "Content-Type": "application/json",
    "x-auth": token,
  };
  return axios.get(`${BASE_URL}overview`, { headers });
}

const FunctionsProvider = {
  adminLogin,
  bookings,
  getBookings,
  getBookingInfo,
  getHospitals,
  createHospitals,
  getHospitalInfo,
  changeBookingInfo,
  deleteBookingInfo,
  getOverview,
  editHospital
};

export default FunctionsProvider;
