import React, { Fragment } from "react";
import { BellIcon, CogIcon } from "@heroicons/react/outline";
import { Menu, Transition } from "@headlessui/react";
import { removeUserSession } from "../../Utils/Session";
import { useNavigate } from "react-router-dom";
import { useSnackbar } from "react-simple-snackbar";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

function AdminHeader() {
  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "16px",
    },
  };

  const [successMsg] = useSnackbar(options);

  let navigate = useNavigate();

  function logout() {
    removeUserSession();
    successMsg("Logged Out Successfully", 5000);
    navigate("/admin/login/");
  }
  return (
    <div className="w-full h-12 bg-white">
      <div className="h-full py-0 px-5 flex justify-between items-center">
        <div className="">
          <span className="font-bold text-2xl text-gray-700 cursor-pointer">
            VaccineBk-19
          </span>
        </div>
        <div className="flex items-center">
          <div className="relative mr-2 top-0 z-40 cursor-pointer">
            <BellIcon className="text-gray-700 w-6 h-6" />
            <span
              className="w-3.5 h-3.5 absolute -top-1 right-0 bg-red-600 text-white rounded-full flex items-center justify-center"
              style={{ fontSize: "10px" }}
            >
              1
            </span>
          </div>
          <div className="relative mr-2 mt-1 z-40 cursor-pointer">
            <Menu
              as="div"
              className="relative inline-block text-left px-3 py-2"
            >
              <div>
                <Menu.Button className="inline-flex items-end justify-center font-medium text-gray-700">
                  <CogIcon className="text-gray-700 w-6 h-6" />
                </Menu.Button>
              </div>

              <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Menu.Items className="origin-top-right absolute right-0 mt-2 w-56 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5 focus:outline-none">
                  <div className="py-1">
                    <Menu.Item>
                      {({ active }) => (
                        <div
                          onClick={logout}
                          className={classNames(
                            active ? "bg-gray-700 text-white" : "text-gray-700",
                            "block px-4 py-2 text-sm cursor-pointer"
                          )}
                        >
                          Log Out
                        </div>
                      )}
                    </Menu.Item>
                  </div>
                </Menu.Items>
              </Transition>
            </Menu>
          </div>
          <img
            src="https://www.seekpng.com/png/detail/514-5147412_default-avatar-icon.png"
            alt=""
            className="w-10 h-10 rounded-full cursor-pointer"
          />
        </div>
      </div>
    </div>
  );
}

export default AdminHeader;
