import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { setUserSession } from "../../Utils/Session";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import { useSnackbar } from "react-simple-snackbar";

function AdminLogin() {
  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    // console.log(value);
    return {
      value,
      onChange: handleChange,
    };
  };

  let navigate = useNavigate();
  const email = useFormInput("");
  const password = useFormInput("");

  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "16px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "16px",
    },
  };
  const [successMsg] = useSnackbar(options);
  const [errorMsg] = useSnackbar(options2);

  // handle button click of login form
  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      let response = await FunctionsProvider.adminLogin(
        email.value,
        password.value
      );
      console.log("response is", response);

      if (response.data.status === "error") {
        errorMsg(response.data.message, 5000);
        return;
      }

      setUserSession(response.data.token, response.data.admin);
      navigate("/admin/dashboard/");
      successMsg(response.data.message, 5000);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  return (
    <div className="bg-white h-screen overflow-hidden flex items-center justify-center">
      <div className="bg-white lg:w-5/12 md:6/12 w-10/12 border border-gray-300 shadow-md">
        <div className="bg-gray-800 absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 rounded-full p-4 md:p-8">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-8 w-8 -mb-6"
            viewBox="0 0 20 20"
            fill="#fff"
          >
            <path
              fill-rule="evenodd"
              d="M3 3a1 1 0 011 1v12a1 1 0 11-2 0V4a1 1 0 011-1zm7.707 3.293a1 1 0 010 1.414L9.414 9H17a1 1 0 110 2H9.414l1.293 1.293a1 1 0 01-1.414 1.414l-3-3a1 1 0 010-1.414l3-3a1 1 0 011.414 0z"
              clip-rule="evenodd"
            />
          </svg>
          ;
        </div>
        <form className="p-12 md:p-24">
          <div className="flex items-center text-lg mb-6 md:mb-8">
            <svg className="absolute ml-3" width="24" viewBox="0 0 24 24">
              <path d="M20.822 18.096c-3.439-.794-6.64-1.49-5.09-4.418 4.72-8.912 1.251-13.678-3.732-13.678-5.082 0-8.464 4.949-3.732 13.678 1.597 2.945-1.725 3.641-5.09 4.418-3.073.71-3.188 2.236-3.178 4.904l.004 1h23.99l.004-.969c.012-2.688-.092-4.222-3.176-4.935z" />
            </svg>
            <input
              type="email"
              name="email"
              {...email}
              className="bg-gray-200 pl-12 py-2 md:py-4 focus:outline-none w-full"
              placeholder="Email Address"
            />
          </div>
          <div className="flex items-center text-lg mb-6 md:mb-8">
            <svg className="absolute ml-3" viewBox="0 0 24 24" width="24">
              <path d="m18.75 9h-.75v-3c0-3.309-2.691-6-6-6s-6 2.691-6 6v3h-.75c-1.24 0-2.25 1.009-2.25 2.25v10.5c0 1.241 1.01 2.25 2.25 2.25h13.5c1.24 0 2.25-1.009 2.25-2.25v-10.5c0-1.241-1.01-2.25-2.25-2.25zm-10.75-3c0-2.206 1.794-4 4-4s4 1.794 4 4v3h-8zm5 10.722v2.278c0 .552-.447 1-1 1s-1-.448-1-1v-2.278c-.595-.347-1-.985-1-1.722 0-1.103.897-2 2-2s2 .897 2 2c0 .737-.405 1.375-1 1.722z" />
            </svg>
            <input
              type="password"
              name="password"
              {...password}
              className="bg-gray-200 pl-12 py-2 md:py-4 focus:outline-none w-full"
              placeholder="Password"
            />
          </div>
          <button
            type="submit"
            onClick={handleLogin}
            className="bg-gradient-to-b from-gray-700 to-gray-900 font-medium p-2 md:p-4 text-white uppercase w-full"
          >
            Login
          </button>
        </form>
      </div>
    </div>
  );
}

export default AdminLogin;
