import React, { useEffect, useState } from "react";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import Header from "../Header";
import AppointmentModal from "./AppointmentModal";
import { BASE_URL } from "../../components/Url";
import dateFormat from "dateformat";
import { useSnackbar } from "react-simple-snackbar";
import { useNavigate } from "react-router-dom";

function CheckAppointment() {
  const [page, setPage] = useState(1);
  const [bookingInfoData, setBookingInfoData] = useState([]);
  const [states, setStates] = useState([]);
  const [selectedState, setSelectedState] = useState([]);
  const [lgas, setLgas] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [hospitalData, setHospitalData] = useState([]);
  const navigate = useNavigate();

  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    console.log(value);
    return {
      value,
      onChange: handleChange,
    };
  };

  const vacId = useFormInput("");
  const dose = useFormInput("");
  const vaccine = useFormInput("");
  const hospital = useFormInput("");
  const date = useFormInput("");
  const sessionTime = useFormInput("");

  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "16px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "16px",
    },
  };
  const [successMsg] = useSnackbar(options);
  const [errorMsg] = useSnackbar(options2);

  useEffect(() => {
    const getStates = async () => {
      const response = await fetch(`${BASE_URL}`);
      const states = await response.json();
      if (typeof states !== "undefined") {
        setStates(states);
      }
    };
    getStates();
  }, []);

  const handleStateChange = () => {
    const stateName = document.getElementById("state").value;
    setSelectedState(stateName);
    if (
      document.getElementById("state").value === "Federal Capital Territory"
    ) {
      setSelectedState("abuja");
    }
  };

  useEffect(() => {
    const getLgas = async () => {
      const response = await fetch(`${BASE_URL}${selectedState}/lgas`);
      const lgas = await response.json();
      if (typeof lgas !== "undefined") {
        setLgas(lgas);
      }
    };
    getLgas();
  }, [selectedState]);

  const handleLgaChange = () => {
    const currentLga = document.getElementById("lga").value;
    getHospitalData(currentLga);
  };

  //endpoint to get hospitals/vaccine centers in an lga
  const getHospitalData = async (currentLga) => {
    try {
      let response = await FunctionsProvider.getHospitals(currentLga);
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        return;
      }
      setHospitalData(response.data.hospitals);
    } catch (error) {
      console.log("Error is", error);
    }
  };

  //endpoint to get the booking Information of a user
  const getBookingInfoData = async () => {
    try {
      let response = await FunctionsProvider.getBookingInfo(vacId.value);
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        return;
      }
      setBookingInfoData(response.data.booking);
    } catch (error) {
      console.log("Error is", error);
    }
  };

  //endpoint to submit the edited booking information of a user
  const editBookingInfo = async () => {
    try {
      let response = await FunctionsProvider.changeBookingInfo(
        vacId.value,
        dose.value,
        vaccine.value,
        hospital.value,
        date.value,
        sessionTime.value
      );
      if (response.data.status === "error") {
        errorMsg("An error occurred. Please try again", 5000);
        return;
      }
      // console.log(response.data);
      navigate("/");
      successMsg("Changes saved Successfully!", 5000);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  return (
    <>
      {openModal && (
        <AppointmentModal closeModal={setOpenModal} vacId={vacId.value} />
      )}
      <Header />
      {page === 1 && (
        <div className="my-20 mx-7 md:mx-auto max-w-xl">
          <div className="flex flex-col text-center">
            <h3 className="text-4xl font-bold">
              Vaccination Appointment Portal
            </h3>
            <p className="font-medium text-base py-6">
              Enter your vaccination ID to edit or cancel your vaccination
              appointment
            </p>
          </div>
          <form className="w-full mx-auto max-w-2xl">
            <input
              type="text"
              {...vacId}
              className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3.5 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
              placeholder="VA-ZL85760456QN"
            />
            <button
              className="w-full bg-gray-700 text-white py-3.5 px-4 border-2 border-gray-700 rounded-lg font-bold hover:bg-transparent hover:text-gray-700 capitalize my-8"
              type="button"
              onClick={() => {
                getBookingInfoData();
                setPage(2);
              }}
            >
              proceed to appointment
            </button>
          </form>
        </div>
      )}
      {page === 2 && (
        <div className="max-w-7xl py-14 mx-auto">
          <div className="flex md:flex-row flex-col items-center mx-3.5 lg:mx-0 space-y-2 md:space-y-0">
            <p
              className="text-base font-bold cursor-pointer text-gray-700 text-left mr-auto md:mr-0"
              onClick={() => setPage(1)}
            >
              Back
            </p>
            <h3 className="justify-center mx-auto font-bold text-3xl text-gray-700 text-center">
              Edit or Cancel Vaccination Appointment
            </h3>
          </div>
          <div className="w-full max-w-5xl mx-auto py-6">
            <div className="flex flex-wrap lg:-mx-3 py-3">
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="gender"
                >
                  Which Covid-19 vaccine dose are you booking for?
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  name="vaccine_dose"
                  {...dose}
                >
                  <option
                    value={bookingInfoData.dose}
                    className="text-gray-400"
                    selected
                  >
                    {bookingInfoData.dose}[]
                  </option>
                  <option value="First">First dose</option>
                  <option value="Second">Second dose</option>
                </select>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="occupation"
                >
                  What vaccine would you like to be vaccinated with?
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  name="vaccine_type"
                  {...vaccine}
                >
                  <option
                    value={bookingInfoData.vaccine}
                    className="text-gray-400"
                    selected
                  >
                    {bookingInfoData.vaccine}
                  </option>
                  <option value="Pfizer">Pfizer</option>
                  <option value="Moderna">Moderna</option>
                  <option value="Johnson &amp; Johnson">
                    Johnson &amp; Johnson
                  </option>
                  <option value="Oxford">Oxford, AstraZeneca</option>
                  <option value="Novavax">Novavax</option>
                </select>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="occupation"
                >
                  Preferred State for Vaccination
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  onChange={handleStateChange}
                  name="state"
                  id="state"
                  defaultValue={bookingInfoData.state}
                >
                  <option value={bookingInfoData.state}>
                    {bookingInfoData.state}
                  </option>
                  {states.map((thisstate, index) => {
                    return (
                      <option key={index} value={thisstate.name}>
                        {/* {console.log(thisstate.name)} */}
                        {thisstate.name}
                      </option>
                    );
                  })}
                </select>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="occupation"
                >
                  Preferred LGA for Vaccination
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  onChange={handleLgaChange}
                  name="lga"
                  id="lga"
                  defaultValue={bookingInfoData.lga}
                >
                  <option value={bookingInfoData.lga}>
                    {bookingInfoData.lga}
                  </option>
                  {lgas.length > 0 ? (
                    lgas.map((thislga, index) => {
                      return (
                        <option key={index} value={thislga}>
                          {thislga}
                        </option>
                      );
                    })
                  ) : (
                    <option value="">-- Select LGA --</option>
                  )}
                </select>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="occupation"
                >
                  Preferred Center for Vaccination
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  name="vaccine_center"
                  onChange={"vaccine_center"}
                  defaultValue={bookingInfoData.hospital}
                  {...hospital}
                >
                  <option value={bookingInfoData.hospital}>
                    {bookingInfoData.hospital}
                  </option>
                  {hospitalData.length > 0 ? (
                    hospitalData.map((thishospital, index) => {
                      return (
                        <option key={index} value={thishospital.name}>
                          {thishospital.name}
                        </option>
                      );
                    })
                  ) : (
                    <option value="Ikoyi Medical Hospital">
                      There's no hospital in this LGA
                    </option>
                  )}
                </select>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  for="grid-first-name"
                >
                  Select your preferred date for vaccination
                </label>
                <input
                  // defaultValue={dateFormat(bookingInfoData.date, "yyyy-mm-dd")}
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  type="date"
                  {...date}
                  placeholder={dateFormat(bookingInfoData.date, "yyyy-mm-dd")}
                  onFocus={(e) => (e.target.type = "date")}
                  onBlur={(e) => (e.target.type = "text")}
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                <label
                  className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                  htmlFor="occupation"
                >
                  Select your preferred time of appointment
                </label>
                <select
                  className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                  name="vaccination_time"
                  defaultValue={bookingInfoData.sessionTime}
                  {...sessionTime}
                >
                  <option
                    value={bookingInfoData.sessionTime}
                    className="text-gray-400"
                  >
                    {bookingInfoData.sessionTime}
                  </option>
                  <option value="Morning">Morning Session</option>
                  <option value="Afternoon">Afternoon Session</option>
                </select>
              </div>
            </div>
            <div className="mx-auto my-10 flex md:flex-row flex-col space-y-4 md:space-y-0 justify-center">
              <button
                className="py-2 px-8 bg-white text-gray-700 border-2 border-gray-700 text-base font-bold rounded-lg mx-3"
                onClick={() => {
                  setOpenModal(true);
                }}
              >
                Cancel Appointment
              </button>
              <button
                className="py-2 px-8 bg-gray-700 text-white text-base font-bold rounded-lg focus:border-4 transition-colors duration-300 transform hover:bg-gray-800 border-gray-700 mx-3"
                type="submit"
                onClick={() => {
                  editBookingInfo();
                }}
              >
                Apply Changes
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default CheckAppointment;
