import React from "react";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import { useSnackbar } from "react-simple-snackbar";
import { useNavigate } from "react-router-dom";

function AppointmentModal({ closeModal, vacId }) {
  // console.log(vacId);
  const navigate = useNavigate();

  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "16px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "16px",
    },
  };
  const [successMsg] = useSnackbar(options);
  const [errorMsg] = useSnackbar(options2);

  //endpoint to delete booking information of a user
  const removeBookingInfo = async () => {
    try {
      let response = await FunctionsProvider.deleteBookingInfo(vacId);
      if (response.data.status === "error") {
        errorMsg(response.data.message, 5000);
        return;
      }
      // console.log(response.data);
      navigate("/");
      successMsg("Appointment Cancelled.", 5000);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };
  return (
    <div className="w-screen h-screen fixed flex justify-center items-center bg-black bg-opacity-70 z-20 transition-opacity ease-in-out duration-500">
      <div
        className="w-full h-full inset-0 z-0 absolute"
        onClick={() => {
          closeModal(false);
        }}
      ></div>
      <div className="max-w-xs md:max-w-2xl z-50 bg-white rounded-2xl p-8">
        <div className="max-w-xl mx-auto">
          <div className="flex flex-col justify-center text-center space-y-3">
            <h4 className="font-bold text-gray-700 text-2xl">
              Are you sure you want to delete this booking?
            </h4>
            <p className="text-sm text-gray-400 font-normal px-6 md:px-20">
              If you delete this appointment booking, it can not be undone. You
              can choose to edit booking instead.{" "}
            </p>
          </div>
          <div className="my-8 flex md:flex-row flex-col justify-center space-y-3 md:space-y-0">
            <button
              className="mx-3 py-4 px-8 bg-transparent border border-gray-700 text-gray-700 rounded-lg"
              onClick={() => {
                closeModal(false);
              }}
            >
              Cancel
            </button>
            <button
              className="mx-3 py-4 px-8 bg-red-600 text-white font-bold rounded-lg"
              type="button"
              onClick={() => removeBookingInfo()}
            >
              Yes, delete
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AppointmentModal;
