import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import AdminHeader from "../AdminHeader";
import Sidebar from "../Sidebar";
import { BASE_URL } from "../../Url";
import FunctionsProvider from "../../../Provider/FunctionsProvider";
import { getToken } from "../../../Utils/Session";
import SuccessModal from "./SuccessModal";
import { useSnackbar } from "react-simple-snackbar";

function AddVaccineCenter() {
  const navigate = useNavigate();
  const vacCenter = () => navigate("/admin/vaccine-center");
  const [selectedState, setSelectedState] = useState([]);
  const [states, setStates] = useState([]);
  const [lgas, setLgas] = useState([]);
  const [token] = useState(getToken());
  const [openModal, setOpenModal] = useState(false);

  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    // console.log(value);
    return {
      value,
      onChange: handleChange,
    };
  };

  const centerName = useFormInput("");
  const facilityId = useFormInput("");
  const centerLga = useFormInput("");

  useEffect(() => {
    const getStates = async () => {
      const response = await fetch(`${BASE_URL}`);
      const states = await response.json();
      if (typeof states !== "undefined") {
        setStates(states);
      }
    };
    getStates();
  }, []);

  const handleStateChange = () => {
    const stateName = document.getElementById("state").value;
    setSelectedState(stateName);
    if (
      document.getElementById("state").value === "Federal Capital Territory"
    ) {
      setSelectedState("abuja");
    }
  };

  useEffect(() => {
    const getLgas = async () => {
      const response = await fetch(`${BASE_URL}${selectedState}/lgas`);
      const lgas = await response.json();
      if (typeof lgas !== "undefined") {
        setLgas(lgas);
      }
    };
    getLgas();
  }, [selectedState]);

  //Snackbar Setup
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "16px",
    },
  };
  const [errorMsg] = useSnackbar(options);

  // endpoint to add a new vaccine center/hospital
  const addVaccineCenter = async () => {
    try {
      let body = {
        name: centerName.value,
        facilityId: facilityId.value,
        state: selectedState,
        lga: centerLga.value,
      };
      let response = await FunctionsProvider.createHospitals(token, body);
      console.log("response is", response);

      if (response.data.status === "error") {
        errorMsg("Error creating center", 5000);
        return;
      }

      setOpenModal(true);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  return (
    <>
      {openModal && <SuccessModal closeModal={setOpenModal} />}
      <AdminHeader />
      <div className="flex mt-2">
        <Sidebar />
        <div className="" style={{ flex: 4 }}>
          <div className="container py-8 px-6">
            <div className="flex flex-col mb-5 space-y-2">
              <p
                className="font-semibold text-sm text-gray-700 cursor-pointer"
                onClick={() => vacCenter()}
              >
                Back
              </p>
              <h3 className="font-semibold text-lg">
                Add a new Vaccine Center
              </h3>
            </div>
            <div>
              <div className="flex flex-wrap lg:-mx-3 py-3">
                <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                  <label
                    className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                    htmlFor="vaccine_center"
                  >
                    Name of Center
                  </label>
                  <input
                    className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                    name="vaccine_center"
                    type="text"
                    {...centerName}
                    placeholder="Ikorodu Medical Center"
                  />
                </div>
                <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                  <label
                    className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                    htmlFor="facilityId"
                  >
                    Facility ID
                  </label>
                  <input
                    className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                    name="facilityId"
                    type="text"
                    {...facilityId}
                    placeholder="HP-001"
                  />
                </div>
              </div>
              <div className="flex flex-wrap lg:-mx-3 py-1">
                <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                  <label
                    className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                    htmlFor="occupation"
                  >
                    Select State
                  </label>
                  <select
                    className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                    onChange={handleStateChange}
                    id="state"
                  >
                    <option value="">-- Select State --</option>
                    {states.map((thisstate, index) => {
                      return (
                        <option key={index} value={thisstate.name}>
                          {/* {console.log(thisstate.name)} */}
                          {thisstate.name}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                  <label
                    className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                    htmlFor="quantity"
                  >
                    Select LGA
                  </label>
                  <select
                    className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                    onChange={""}
                    id="lga"
                    {...centerLga}
                    defaultValue={""}
                  >
                    {lgas.length > 0 ? (
                      lgas.map((thislga, index) => {
                        return (
                          <option key={index} value={thislga}>
                            {thislga}
                          </option>
                        );
                      })
                    ) : (
                      <option value="">-- Select LGA --</option>
                    )}
                  </select>
                </div>
              </div>
              <button
                className="bg-gray-700 text-white py-3 px-5 border-2 border-gray-700 rounded-lg font-bold hover:bg-transparent hover:text-gray-700 capitalize my-4"
                type="button"
                onClick={() => {
                  addVaccineCenter();
                }}
              >
                create
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AddVaccineCenter;
