import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./components/Home";
import Booking from "./components/BookingForms/Booking";
import CheckStatus from "./components/Status/CheckStatus";
import CheckAppointment from "./components/Appointment/CheckAppointment";
import AdminLogin from "./components/Admin/AdminLogin";
import AdminDashboard from "./components/Admin/AdminDashboard";
import AdminVaccineCenter from "./components/Admin/AdminVaccineCenter";
import AdminBookings from "./components/Admin/AdminBookings";
import AddVaccineCenter from "./components/Admin/addVaccineCenter/AddVaccineCenter";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route exact path="/" element={<Home />} />
        <Route path="/booking" element={<Booking />} />
        <Route path="/check-status" element={<CheckStatus />} />
        <Route path="/check-appointment" element={<CheckAppointment />} />
        <Route path="/admin/login/" element={<AdminLogin />} />
        <Route path="/admin/dashboard/" element={<AdminDashboard />} />
        <Route path="/admin/vaccine-center" element={<AdminVaccineCenter />} />
        <Route
          path="/admin/add-vaccine-center"
          element={<AddVaccineCenter />}
        />
        <Route path="/admin/bookings" element={<AdminBookings />} />
      </Routes>
    </div>
  );
}

export default App;
