import React, { useRef, useState } from "react";
import emailjs from "emailjs-com";
import { useSnackbar } from "react-simple-snackbar";
import { Link } from "react-router-dom";
import Header from "../Header";
import FunctionsProvider from "../../Provider/FunctionsProvider";

function ConfirmBooking({ prevStep, values }) {
  const {
    firstName,
    lastName,
    email,
    phoneNumber,
    address,
    gender,
    occupation,
    dob,
    idType,
    booking_for,
    dose,
    vaccine,
    stateName,
    lga,
    hospital,
    date,
    sessionTime,
  } = values;

  const [page, setPage] = useState(1);

  const [confirmData, setConfirmData] = useState([]);

  const form = useRef();

  /** Snackbar Setup */
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "17px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "14px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "17px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "14px",
    },
  };
  const [successMsg] = useSnackbar(options);
  const [errorMsg] = useSnackbar(options2);

  //send email to users
  const sendEmail = (e) => {
    emailjs
      .sendForm(
        "service_x8gbp1h",
        "template_mk5zvtm",
        form.current,
        "h57xgWcKg7L1rL6FO"
      )
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const handleBooking = async (e) => {
    e.preventDefault();
    try {
      let response = await FunctionsProvider.bookings({
        firstName: values.firstName,
        lastName: values.lastName,
        email: values.email,
        phoneNumber: values.phoneNumber,
        address: values.address,
        gender: values.gender,
        occupation: values.gender,
        dob: values.dob,
        idType: values.idType,
        booking_for: values.booking_for,
        dose: values.dose,
        vaccine: values.vaccine,
        stateName: values.stateName,
        lga: values.lga,
        hospital: values.hospital,
        date: values.date,
        sessionTime: values.sessionTime,
      });
      console.log("response is", response);

      if (response.data.status === "error") {
        errorMsg("response.data.message", 5000);
        return;
      }
      sendEmail();
      setConfirmData(response.data.booking);
      setPage(2);
      successMsg("Email sent successfully", 5000);
    } catch (error) {
      errorMsg(error.response.data.message, 5000);
      console.log("Error is", error);
    }
  };

  console.log(confirmData);

  return (
    <>
      <Header />
      {page === 1 && (
        <>
          {/* /****** STEPPER ******/}
          <div className="max-w-lg mx-auto p-4 flex justify-between items-center overflow-x-hidden md:overflow-x-visible">
            <div className="relative flex flex-col items-center text-gray-700">
              <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase text-gray-400">
                {/* Display Description */}
                Step 1
              </div>
              <div className="rounded-full transition duration-500 ease-in-out bg-gray-800 border-2 border-gray-800 text-white font-bold h-6 w-6 flex items-center justify-center p-4">
                {/* Display Number */} &#10003;
              </div>
            </div>
            <div className="flex-auto border-t-2 transition duration-500 ease-in-out border-gray-700">
              {/* Display Line */}
            </div>
            <div className="relative flex flex-col items-center text-gray-700">
              <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase text-gray-400">
                {/* Display Description */}
                Step 2
              </div>
              <div className="rounded-full transition duration-500 ease-in-out bg-gray-800 border-2 border-gray-800 text-white font-bold h-6 w-6 flex items-center justify-center p-4">
                {/* Display Number */} &#10003;
              </div>
            </div>
            <div className="flex-auto border-t-2 transition duration-500 ease-in-out border-gray-700">
              {/* Display Line */}
            </div>
            <div className="relative flex flex-col items-center text-gray-700">
              <div className="absolute top-0 text-center mt-16 w-32 text-xs font-medium uppercase">
                {/* Display Description */}
                Step 3
              </div>
              <div className="rounded-full transition duration-500 ease-in-out border-2 border-gray-300 h-6 w-6 flex items-center justify-center p-4">
                {/* Display Number */} 3
              </div>
            </div>
          </div>
          {/* /***** END OF STEPPER *****/}
          <div className="max-w-7xl py-14 mx-auto">
            <h2 className="text-black font-bold text-3xl flex justify-center">
              Confirm your details
            </h2>
            <form ref={form} onSubmit={handleBooking}>
              <div className="w-full max-w-5xl mx-auto py-6">
                <h4 className="font-semibold text-lg px-3 lg:px-0 mb-3">
                  Personal Information
                </h4>
                <p className="font-bold text-base px-3 lg:px-0 mt-3">
                  I am booking this appointment and/or registering for:
                </p>
                <div className="block py-2 px-3 lg:px-0">{booking_for}</div>
                <div className="flex flex-wrap lg:-mx-3 py-3">
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="gender"
                    >
                      Last Name (Surname)
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="lastName"
                      value={lastName}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      First Name (Given name)
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="firstName"
                      value={firstName}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      Email
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="email"
                      value={email}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      Phone Number
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="phoneNumber"
                      value={phoneNumber}
                    />
                  </div>
                </div>
                <div className="w-full px-3 lg:px-0 py-1">
                  <label
                    className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                    htmlFor="address"
                  >
                    Address
                  </label>
                  <input
                    className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                    name="address"
                    value={address}
                  />
                </div>
                <div className="flex flex-wrap lg:-mx-3 py-3">
                  <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="gender"
                    >
                      Select Gender
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="gender"
                      value={gender}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      for="occupation"
                    >
                      Occupation
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="occupation"
                      value={occupation}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      for="occupation"
                    >
                      Date of birth
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="dob"
                      value={dob}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      for="occupation"
                    >
                      Type of ID
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="idType"
                      value={idType}
                    />
                  </div>
                </div>
              </div>
              <div className="w-full max-w-5xl mx-auto py-6">
                <h4 className="font-semibold text-lg px-3 lg:px-0">
                  Vaccine Information
                </h4>
                <div className="flex flex-wrap lg:-mx-3 py-3">
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="gender"
                    >
                      Which Covid-19 vaccine dose are you booking for?
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="dose"
                      value={dose}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      What vaccine would you like to be vaccinated with?
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="vaccine"
                      value={vaccine}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      Preferred State for Vaccination
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      value={stateName}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="occupation"
                    >
                      Preferred LGA for Vaccination
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      value={lga}
                    />
                  </div>
                </div>
                <div className="flex flex-wrap lg:-mx-3">
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      htmlFor="gender"
                    >
                      Preferred Center for Vaccination
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="hospital"
                      value={hospital}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      for="date"
                    >
                      Select your preferred date for vaccination
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="date"
                      value={date}
                    />
                  </div>
                  <div className="w-full md:w-1/2 px-3 mb-3 md:mb-2">
                    <label
                      className="block tracking-wide text-gray-700 text-base font-bold mb-2"
                      for="occupation"
                    >
                      Select your preferred time of appointment
                    </label>
                    <input
                      className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
                      name="sessionTime"
                      value={sessionTime}
                    />
                  </div>
                </div>
              </div>
              <div
                className="max-w-5xl mx-3 lg:mx-auto py-6 px-10"
                style={{ backgroundColor: "#EDF0F5" }}
              >
                <ul className="font-normal text-base space-y-3">
                  <li className="list-disc">
                    Please check your details carefully before clicking the{" "}
                    <span className="font-bold">CONFIRM</span> button.
                  </li>
                  <li className="list-disc">
                    You will receive an email confirming your appointment.
                  </li>
                  <li className="list-disc">
                    To ensure you receive your appointment confirmation email,
                    we recommend you use your personal email as some companies
                    may block these notifications. Please be sure to check your
                    spam or junk folder if you do not see this in your inbox
                  </li>
                  <li className="list-disc">
                    Ensure you take note of the{" "}
                    <span className="font-bold">VACCINATION ID</span> in the
                    next page. This will be your ticket to checking your status
                    or editing your appointment after booking.
                  </li>
                </ul>
              </div>

              <div className="mx-auto mt-10 flex justify-center">
                <button
                  className="py-2 px-8 bg-white text-gray-700 border-2 border-gray-700 text-base font-bold rounded-lg mx-3"
                  onClick={prevStep}
                >
                  Back
                </button>
                <button
                  className="py-2 px-8 bg-gray-700 text-white text-base font-bold rounded-lg focus:border-4 transition-colors duration-300 transform hover:bg-gray-800 border-gray-700"
                  type="submit"
                >
                  Confirm
                </button>
              </div>
            </form>
          </div>
        </>
      )}
      {page === 2 && (
        <div
          className="max-w-5xl my-16 mx-auto py-12 px-11 lg:px-32"
          style={{ backgroundColor: "#E0E0E0" }}
        >
          <div className="mx-auto flex justify-center">
            <svg
              width="128"
              height="128"
              viewBox="0 0 128 128"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M114 70.0001C110 90.0001 94.9198 108.832 73.7598 113.04C63.4398 115.095 52.7343 113.842 43.1679 109.459C33.6014 105.076 25.6616 97.7868 20.479 88.6288C15.2964 79.4709 13.1352 68.9112 14.3031 58.4535C15.4711 47.9958 19.9086 38.1731 26.9838 30.3841C41.4958 14.4001 65.9998 10.0001 85.9998 18.0001"
                stroke="#00913D"
                stroke-width="12"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M46 62L66 82L114 30"
                stroke="#00913D"
                stroke-width="12"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </div>
          <h3 className="my-8 text-2xl lg:text-3xl font-bold text-center mx-auto">
            Vaccination Booking Confirmed
          </h3>
          <div className="flex justify-between items-center my-14">
            <div className="space-y-3 md:space-y-2 lg:space-y-1">
              <p>Name:</p>
              <p>Vac ID:</p>
              <p>Dose:</p>
              <p>Vaccine:</p>
              <p>Date:</p>
              <p>Time:</p>
              <p>Facility ID:</p>
              <p>Health Facility:</p>
              <p>LGA:</p>
              <p>State:</p>
            </div>
            <div className="text-right space-y-4 md:space-y-2 lg:space-y-1">
              <p>{confirmData.name}</p>
              <p>{confirmData.vacId}</p>
              <p>{confirmData.dose} dose</p>
              <p>{confirmData.vaccine}</p>
              <p>{new Date(confirmData.date).toDateString("Y")}</p>
              <p>{confirmData.sessionTime}</p>
              <p>{confirmData.facilityId}</p>
              <p>{confirmData.hospital}</p>
              <p>{confirmData.lga}</p>
              <p>{confirmData.state}</p>
            </div>
          </div>
          <div
            className="max-w-5xl mx-3 lg:mx-auto py-6 px-10"
            style={{ backgroundColor: "#EDF0F5" }}
          >
            <ul className="font-normal text-base space-y-3">
              <li className="list-disc">
                We have sent you an email with all the details. we will also
                send you reminders.
              </li>
              <li className="list-disc">
                Please note you would be required to present your preregistered
                vaccination ID at the facility.
              </li>
              <li className="list-disc">
                You will be asked to provide proof of identity and eligibility
                as your appointment
              </li>
            </ul>
          </div>
          <div className="mx-auto mt-10 flex justify-center">
            <Link to="/">
              <button className="bg-transparent border-2 border-gray-700 rounded-lg py-4 px-10 text-gray-700 font-bold hover:bg-gray-700 hover:text-white">
                Back To Home
              </button>
            </Link>
          </div>
        </div>
      )}
    </>
  );
}

export default ConfirmBooking;
