import React, { useState, useEffect } from "react";
import AdminHeader from "./AdminHeader";
import Sidebar from "./Sidebar";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import { getToken } from "../../Utils/Session";
import { useSnackbar } from "react-simple-snackbar";
import { useNavigate } from "react-router-dom";

function AdminDashboard() {
  const [token] = useState(getToken());
  const [bookingData, setBookingData] = useState([]);
  const [overviewData, setOverviewData] = useState([]);
  const navigate = useNavigate();

  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "17px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "14px",
    },
  };
  const [errorMsg] = useSnackbar(options);

  //endpoint to display booking information on the admin dashboard
  const getBookingData = async () => {
    try {
      let response = await FunctionsProvider.getBookings(token);
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        errorMsg("error fetching data", 5000);
        return;
      }
      setBookingData(response.data.bookings);
    } catch (error) {
      console.log("Error is", error);
    }
  };

  //endpoint to display overview data on the admin dashboard
  const getOverviewInfo = async () => {
    try {
      let response = await FunctionsProvider.getOverview(token);
      // console.log("response data is", response.data);
      if (response.data.status === "error") {
        errorMsg("error fetching data", 5000);
        return;
      }
      setOverviewData(response.data.overview);
    } catch (error) {
      console.log("Error is", error);
    }
  };
  useEffect(() => {
    getBookingData();
    getOverviewInfo();
  }, []);

  return (
    <>
      <AdminHeader />
      <div className="flex mt-2">
        <Sidebar />
        <div className="" style={{ flex: 4 }}>
          <section className="container px-6 py-4 mx-auto">
            <div className="grid gap-6 mb-8 md:grid-cols-2 lg:grid-cols-3">
              <div className="flex items-center p-4 bg-white border-2 border-gray-200 rounded-lg shadow-sm">
                <div className="p-3 mr-4 bg-blue-500 text-white rounded-full">
                  <svg
                    className="w-6 h-6"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M5 19a2 2 0 01-2-2V7a2 2 0 012-2h4l2 2h4a2 2 0 012 2v1M5 19h14a2 2 0 002-2v-5a2 2 0 00-2-2H9a2 2 0 00-2 2v5a2 2 0 01-2 2z"
                    ></path>
                  </svg>
                </div>
                <div>
                  <p className="mb-2 text-lg font-medium text-gray-900">
                    Total Bookings
                  </p>
                  <p className="text-base font-normal text-gray-800">
                    {overviewData.noOfBookings}
                  </p>
                </div>
              </div>
              <div className="flex items-center p-4 bg-white border-2 border-gray-200 rounded-lg shadow-sm">
                <div className="p-3 mr-4 bg-green-500 text-white rounded-full">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      d="M12 9v3m0 0v3m0-3h3m-3 0H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
                    />
                  </svg>
                </div>
                <div>
                  <p className="mb-2 text-lg font-medium text-gray-900">
                    Total Vaccines
                  </p>
                  <p className="text-base font-normal text-gray-800">
                    {overviewData.noOfVaccines}
                  </p>
                </div>
              </div>
              <div className="flex items-center p-4 bg-white border-2 border-gray-200 rounded-lg shadow-sm">
                <div className="p-3 mr-4 bg-orange-500 text-white rounded-full">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      d="M8 14v3m4-3v3m4-3v3M3 21h18M3 10h18M3 7l9-4 9 4M4 10h16v11H4V10z"
                    />
                  </svg>
                </div>
                <div>
                  <p className="mb-2 text-lg font-medium text-gray-900">
                    Vaccine Centers
                  </p>
                  <p className="text-base font-normal text-gray-800">
                    {overviewData.noOfVaccineCenters}
                  </p>
                </div>
              </div>
            </div>
          </section>
          <div className="container flex justify-between items-center py-8 px-6">
            <div className="flex flex-col space-y-2">
              <h3 className="font-semibold text-lg">Bookings</h3>
              <p className="text-sm font-normal text-gray-400">
                Brief list of bookings made so far
              </p>
            </div>
            <div>
              <button
                className="bg-gray-700 py-3 px-7 text-white font-medium rounded-lg"
                onClick={() => navigate("/admin/bookings")}
              >
                View All
              </button>
            </div>
          </div>
          <div
            className="container flex mx-auto shadow rounded-lg overflow-hidden"
            style={{ maxWidth: "73rem" }}
          >
            <table className="min-w-full leading-normal">
              <thead>
                <tr>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    Name
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    Email
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    Dose
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    Vaccine
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    State
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    LGA
                  </th>
                  <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-sm font-medium text-gray-600 uppercase tracking-wider">
                    Center
                  </th>
                </tr>
              </thead>
              <tbody>
                {bookingData.length > 0 ? (
                  // eslint-disable-next-line array-callback-return
                  bookingData.map((bkdata, index) => {
                    if (index < 5) {
                      return (
                        <tr key={index}>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.name}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.email}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.dose}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.vaccine}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.state}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.lga}
                            </p>
                          </td>
                          <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {bkdata.hospital}
                            </p>
                          </td>
                        </tr>
                      );
                    }
                  })
                ) : (
                  <tr className="flex justify-center mx-auto py-3">
                    <p className="text-gray-900 text-base whitespace-no-wrap">
                      No Booking has been made.
                    </p>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}

export default AdminDashboard;
