import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useSnackbar } from "react-simple-snackbar";
import FunctionsProvider from "../../Provider/FunctionsProvider";
import Header from "../Header";

function CheckStatus() {
  const [page, setPage] = useState(1);
  const [bookingInfoData, setBookingInfoData] = useState("");

  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    // console.log(value);
    return {
      value,
      onChange: handleChange,
    };
  };

  const vacId = useFormInput("");

  /** Snackbar setup **/
  const options = {
    position: "bottom-right",
    style: {
      backgroundColor: "#28a745",
      border: "1px solid #28a745",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "lightcoral",
      fontSize: "16px",
    },
  };
  const options2 = {
    position: "bottom-right",
    style: {
      backgroundColor: "#df4759",
      border: "1px solid #df4759",
      color: "white",
      fontFamily: "Open Sans, sans-serif",
      fontSize: "18px",
      textAlign: "center",
    },
    closeStyle: {
      color: "#f5f5f5",
      fontSize: "16px",
    },
  };
  const [successMsg] = useSnackbar(options, options2);
  const [errorMsg] = useSnackbar(options2);

  const getBookingInfoData = async () => {
    try {
      let response = await FunctionsProvider.getBookingInfo(vacId.value);
      console.log("response data is", response.data);
      if (response.data.status === "error") {
        setPage(1);
        errorMsg("Incorrect Vaccination ID", 5000);
        return;
      }
      setBookingInfoData(response.data.booking);
      successMsg("Successful", 5000);
    } catch (error) {
      console.log("Error is", error);
      errorMsg(error.response.data.message, 5000);
    }
  };

  const currentDate = new Date();
  const bookingDate = new Date(bookingInfoData.date);

  const time1 = currentDate.getTime();
  const time2 = bookingDate.getTime();

  console.log(currentDate, bookingDate, time1, time2);

  return (
    <>
      <Header />
      {page === 1 && (
        <div className="my-20 mx-7 md:mx-auto max-w-lg">
          <div className="flex flex-col text-center">
            <h3 className="text-4xl font-bold">Vaccination Status Portal</h3>
            <p className="font-medium text-base py-6">
              Enter your vaccination ID to verify your vaccination status
            </p>
          </div>
          <form className="w-full mx-auto max-w-2xl">
            <input
              type="text"
              name="vacId"
              {...vacId}
              className="appearance-none block w-full bg-transparent text-gray-700 border border-gray-400 rounded py-3.5 px-4 leading-tight focus:outline-none focus:bg-white focus:border-green-300"
              placeholder="VA-ZL85760456QN"
            />
            <button
              className="w-full bg-gray-700 text-white py-3.5 px-4 border-2 border-gray-700 rounded-lg font-bold hover:bg-transparent hover:text-gray-700 capitalize my-8"
              type="button"
              onClick={() => {
                getBookingInfoData();
                setPage(2);
              }}
            >
              check status
            </button>
          </form>
        </div>
      )}
      {page === 2 && (
        <div
          className="max-w-5xl my-16 mx-auto py-12 px-11 lg:px-32"
          style={{ backgroundColor: "#E0E0E0" }}
        >
          <div className="mx-auto flex justify-center w-32 h-32">
            <img
              src="../../../assets/img/process_icon_157057.svg"
              alt="status_icon"
            />
          </div>
          <h3 className="my-8 text-2xl lg:text-3xl font-bold text-center mx-auto">
            Vaccination Status
          </h3>
          <div className="flex justify-between items-center my-14">
            <div className="space-y-3 md:space-y-2 lg:space-y-1">
              <p>Name:</p>
              <p>Vac ID:</p>
              <p>Dose:</p>
              <p>Vaccine:</p>
              <p>Date:</p>
              <p>Time:</p>
              <p>Facility ID:</p>
              <p>Health Facility:</p>
              <p>LGA:</p>
              <p>State:</p>
            </div>
            <div className="text-right space-y-4 md:space-y-2 lg:space-y-1">
              <p>{bookingInfoData.name}</p>
              <p>{bookingInfoData.vacId}</p>
              <p>{bookingInfoData.dose}</p>
              <p>{bookingInfoData.vaccine}</p>
              <p>{new Date(bookingInfoData.date).toDateString("Y")}</p>
              <p>{bookingInfoData.sessionTime}</p>
              <p>{bookingInfoData.facilityId}</p>
              <p>{bookingInfoData.hospital}</p>
              <p>{bookingInfoData.lga}</p>
              <p>{bookingInfoData.state}</p>
            </div>
          </div>
          <div className="flex justify-between items-center my-8">
            <h5 className="font-bold text-lg">STATUS:</h5>
            <div>
              {time1 < time2 && (
                <p className="text-yellow-600 italic text-base font-semibold">
                  Pending
                </p>
              )}
              {time1 > time2 && (
                <p className="text-green-600 uppercase text-base font-semibold">
                  Appointment Elapsed
                </p>
              )}
            </div>
          </div>
          <div className="mx-auto mt-12 flex justify-center">
            <Link to="/">
              <button className="bg-transparent border-2 border-gray-700 rounded-lg py-4 px-10 text-gray-700 font-bold hover:bg-gray-700 hover:text-white">
                Back To Home
              </button>
            </Link>
          </div>
        </div>
      )}
    </>
  );
}

export default CheckStatus;
